<template>
  <div id="app">
    <iframe src="../../static/waterfall.html" frameborder="0" width="100%" height="100%"></iframe>
    <div v-for="item in list" :key="item.style">
      <a class='url' name='url' :style=item.style :href=item.url @mouseenter='stopTimer' @mouseleave="startTimer">{{item.name}}</a>
    </div>
  </div>
</template>

<script>
import urls from '../config/url.js'
import { setInterval, setTimeout, clearInterval } from 'timers';
export default {
  name: 'App',
  timer: '',
  data: function(){
    return {
      list: []
    }
  },
  mounted: function(){
    this.initData();
    this.timer = setInterval(this.drop,100)
  },
  methods: {
    initData: function(){
      var _this = this;
      //生成颜色数组
      urls.forEach(function(item,id){
        var obj = {
          name: item.name,
          url: item.url,
          style: '',
          size: 0,
          top: 0,
          left: 0
        }
        _this.combineStyle(obj)
        //碰撞检测
        if(_this.check(obj)==1){
          _this.list.push(obj);
        }else{
          id--;
        }
      })
    },
    //生成随机颜色
    getRandomColor: function(){
       return '#'+('00000'+(Math.random()*0x1000000<<0).toString(16)).slice(-6);
    },
    //合成样式
    combineStyle: function(obj){
      var width = document.body.clientWidth - 100;
      var height = document.body.clientHeight - 50;
      obj.top = _.random(10,height);
      obj.left = _.random(10,width);
      obj.size = _.random(10,50);
      obj.style += `color:${this.getRandomColor()};`;
      obj.style += `font-size:${obj.size}px;`;
      obj.style += `line-height:${obj.size}px;`;
      obj.style += `fontWeight:${_.random(200,800)}px;`;
      obj.style += `top:${obj.top}px;`;
      obj.style += `left:${obj.left}px;`;
      obj.style += `text-shadow: ${_.random(-5,5)}px ${_.random(-5,5)}px 0 #fff}`;
    },
    //碰撞检测
    check: function(obj){
      for(var item of this.list){
        var left = Math.abs(obj.left-item.left)
        var top = Math.abs(obj.top-item.top)
        if(!((left>item.size*item.name.length && left>obj.size*obj.name.length) || (top>item.size*item.name.length && top>obj.size*obj.name.length))){
          return 0
        }
      }
      return 1
    },
    //所有的文字都往下掉
    drop: function(){
      for(var i in this.list){
        var styleList = this.list[i].style.split(';');
        if(parseInt(styleList[4].slice(4).slice(0,-2)) < document.body.clientHeight - 50){
          styleList[4] = `top:${parseInt(styleList[4].slice(4).slice(0,-2)) + _.random(0,50)}px`;
        }else{
          styleList[4] = `top:0px`;
        }
        
        this.list[i].style = styleList.join(';');
      }
    },
    stopTimer: function(){
      clearInterval(this.timer)
    },
    startTimer: function(){
      this.timer = setInterval(this.drop,100)
    }
  }
}
</script>

<style>
  * {
    margin: 0px;
    padding: 0px;
  }
  #app{
    height: 100%;
    
  }
  html,body{
    height: 100%;
    background-color: #111e24
  }
  .url{
    font: 5em Brush Script MT; 
    position:absolute;
    text-decoration:none;
  }
  .iframe{
    z-index: -1;
    position: absolute;
  }

</style>



